.van-notify {
  text-align: center;
  word-wrap: break-word;
  padding: 0.256rem 0.64rem;
  padding: var(--notify-padding, 0.256rem 0.64rem);
  font-size: 0.59733rem;
  font-size: var(--notify-font-size, 0.59733rem);
  line-height: 0.85333rem;
  line-height: var(--notify-line-height, 0.85333rem);
}
.van-notify__container {
  position: fixed;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 100%;
  z-index: 1500;
}
.van-notify--primary {
  background-color: #1989fa;
  background-color: var(--notify-primary-background-color, #1989fa);
}
.van-notify--success {
  background-color: #07c160;
  background-color: var(--notify-success-background-color, #07c160);
}
.van-notify--danger {
  background-color: #ee0a24;
  background-color: var(--notify-danger-background-color, #ee0a24);
}
.van-notify--warning {
  background-color: #ff976a;
  background-color: var(--notify-warning-background-color, #ff976a);
}
